@mixin mobile{
    @media screen and (max-width: 480px){
        @content;
    }
}
@mixin tablet{
    @media screen and (max-width: 768px){
       @content;
        }
    }
@mixin laptop{
    @media screen and (max-width: 1200px){
        @content;
        }
}

.formContainer{
    background-color: pink;
    height:100vh;
    display: flex;
    align-items: center;
    justify-content: center;

.formWrapper{
    background-color: rgba(245, 245, 245, 0.758);
    padding: 20px 60px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    .logo{
        color:palevioletred;
        font-weight: bolder;
        font-size: 24px;
      
    }
    .title{
        color: palevioletred;
        font-size: 15px;
    }
    form{
        display: flex;
        flex-direction: column;
        gap: 15px;
        input{
            padding: 15px;
            border: none;
            width: 250px;
            border-bottom: 1px solid palevioletred ;
        }
        button{
            background-color: palevioletred;
            color: white;
            padding: 10px;
            font-weight: bold;
            border:none;
            cursor: pointer;
        }
        label{
           display: flex;
           align-items: center; 
           gap: 10px;
           color: palevioletred;
           font-size: 12px;
           cursor: pointer;

           img{
            width: 32px;
        }
        }
    }
    p{
        color: palevioletred;
        font-size: 12px;
        margin-top: 10px;
    }
}
}

.home{
    background-color: rgba(255, 192, 203, 0.604);
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;


    .container{
        border:1px solid white;
        border-radius: 10px;
        width: 65%;
        height: 80%;
        display: flex;
        overflow: hidden;
        @include tablet{
            width: 90px;
        }

        .sidebar{ 
            flex: 1; 
            background-color:rgb(232, 85, 110);
            position: relative;

            .navbar{
                display: flex;
                align-items: center;
                background-color: rgb(175, 24, 74);
                height: 50px;
                padding: 10px;
                justify-content: space-between;
                color: white;

                .logo{
                    font-weight:bold;
                    @include tablet{
                        display: none;
                    }
                }

                .user{
                    display: flex;
                    gap: 10px;

                    img{
                        background-color:#ddddf7 ;
                        height: 24px;
                        width: 24px;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    button{
                        background-color:palevioletred;
                        color: white;
                        font-weight: bolder;
                        cursor: pointer;
                        font-size: 10px;
                        border: none;
                        border-radius: 50px;
                        @include tablet{
                            position: absolute;
                            bottom: 10px;
                        }
                    }
                }
            }
            .search{
                border-bottom: 1px solid black;

                .searchForm{
                    padding: 10px;
                    input{
                        background-color:transparent ;
                        border: none;
                        color: black;
                        outline: none;
                        &::placeholder {
                            color: white;
                        }
                         
                    }
                }
            }
            
            .userChat {
                padding: 10px;
                display: flex;
                align-items: center;
                gap: 10px;
                color: white;
                cursor: pointer;

                &:hover {
                    background-color: palevioletred;
                }
                img {
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    object-fit: cover;
                }
                .userChatInfo {
                    span  { 
                        font-size: 18px;
                        font-weight: 500;
                    }
                    p {
                        font-size: 14px;
                        color: white;
                    }
                }
            }
        }
        .chat
        { 
            flex: 2;
            
            .chatInfo{
                height: 50px;
                background-color  :rgb(232, 85, 110);
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px;
                color: white;
            }
            .chatIcons{
                display: flex;
                gap: 10px;
                img{
                    height: 24px;
                    cursor: pointer;
                }
            }
            .messages{
                background-color: rgba(255, 192, 203, 0.604);
                padding: 10px;
                height: calc(100% - 160px);
                overflow: scroll;
                
                

                .message{
                    display: flex;
                    gap:20px;
                    margin-bottom: 20px;

                    .messageInfo{
                        display: flex;
                        flex-direction: column;
                        color: whitesmoke;
                        font-weight: 300;

                        img{
                            width:40px;
                            height: 40px;
                            border-radius: 50px;
                            object-fit: cover;
                        }
                    }
                    .messageContent{
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap:10px;


                        p{
                            background-color: white;
                            padding: 10px 20px;
                            border-radius:0px 10px 10px 10px ;
                            max-width: max-content;
                        }
                        img{
                            width: 50%;

                        }
                    }
                    &.owner{
                        flex-direction: row-reverse;

                        .messageContent{
                            align-items: flex-end;
                        }

                        p{
                            background-color: palevioletred;
                            color: white;
                            border-radius: 10px 0px 10px 10px;
                           
                        }
                    }
                }
            }
            .input{
                height:50px ;
                background-color: white;
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between;

                input{
                    width: 100%;
                    border: none;
                    outline: none;
                    color: black;
                    font-size: 18px;

                    &::placeholder{
                        color: lightgray;
                    }
                }
                .send{
                    display: flex;
                    align-items: center;
                    gap:10px;

                    img{
                        height:24px;
                        cursor: pointer;
                    }
                    button{
                        border:none;
                        padding: 10px 15px;
                        color: white;
                        background-color: rgb(232, 85, 110);
                        cursor: pointer;
                    }
                }
            }
        }
    }
}